body {
	margin: 0px;
	padding: 0px;
  // font-family: "Open Sans", "Arial", sans-serif;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

fieldset,
input,
textarea {
  border: none;
}

* {
  box-sizing: border-box;
}

fieldset {
  padding-left: 1px;
  padding-right: 1px;
}

textarea {
  overflow: auto;
  resize: none;
}



